.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about__pic {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;

  .about__pic-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);

    &:hover {
      transform: rotate(0);
    }
  }
}

.about__content {
  .about__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;

    .about__card {
      background: var(--color-bg-variant);
      border: 1px solid transparent;
      border-radius: 1rem;
      padding: 2rem;
      text-align: center;
      transition: var(--transition);

      &:hover {
        background: transparent;
        border-color: var(--color-primary-variant);
        cursor: default;
      }

      .about__icon {
        color: var(--color-primary);
        font-size: 1.4rem;
        margin-bottom: 1rem;
      }

      h5 {
        color: var(--color-white);
        font-size: 0.95rem;
      }

      small {
        color: var(--color-light);
      }
    }
  }

  p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
  }
}

@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
    margin-bottom: 6rem;
  }

  .about__pic {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about__content {
    p {
      margin: 1rem 0 1.5rem;
    }
  }
}

@media screen and (max-width: 600px) {
  .about__container {
    width: var(--container-width-md);
  }

  .about__pic {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__content {
    text-align: center;

    .about__cards {
      grid-template-columns: 1fr;
      gap: 1rem;
    }

    p {
      margin: 1.5rem 0;
    }
  }
}
